.menuListWapper {
    text-align: right;
    width: 97%;
}

.menuList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: end;
        justify-content: flex-end;
    padding-top: 10px;
}

.menuListItem {
    font-size: 32px;
    padding: 10px;
    list-style: none;
    opacity: 0;
    -webkit-animation: fadeIn 0.5s ease 0s 1 forwards;
            animation: fadeIn 0.5s ease 0s 1 forwards;
}

.menuListItem a {
    text-decoration: none;
    color: #c9cacc;
}

.menuListItem a:hover {
    color: #2bbc8a;
}

.list_icon {
    width: 32px;
    height: 32px;
    vertical-align: text-bottom;
    padding-left: 20px;
}

@-webkit-keyframes fadeIn {
    0% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1
    }
}

@keyframes fadeIn {
    0% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1
    }
}


