.main {
    margin-left: 10px;
    padding: 10px;
}

.main h1 {
    font-size: 40px;
    opacity: 0;
    -webkit-animation: fadeIn 0.5s ease 0.7s 1 forwards;
            animation: fadeIn 0.5s ease 0.7s 1 forwards;
}

.main p {
    color: #2bbc8a;
    opacity: 0;
    -webkit-animation: fadeIn 0.5s ease 1s 1 forwards;
            animation: fadeIn 0.5s ease 1s 1 forwards;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
}

.portfolio {
    margin: 20px;
}

.section {
    margin: 50px;
    text-align: center;
}

.section h3 {
    color: #2bbc8a;
}

@-webkit-keyframes fadeIn {
    0% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1
    }
}

@keyframes fadeIn {
    0% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
        opacity: 1
    }
}


